* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.todo-container {
  width: 800px;
  margin: 100px auto 0;
  background-color: #e9e9e9;
  font-family: sans-serif;
  color: #333;
  padding: 15px;
  font-size: 22px;
  animation-name: afterLoad;
  animation-duration: 0.8s;
  h1 {
    text-align: center;
    margin-bottom: 25px;
    margin-top: 12px;
  }
  .todo-inputs {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    input {
      width: 100%;
      display: inline-block;
      padding: 15px 20px;
      background-color: #ffffffd7;
      border: 1px solid #ddd;
      font-size: 20px;
      transition: border-color 0.2s ease-in-out;
      &:focus {
        outline: none;
        border-color: #009688;
      }
    }
    .todo-add {
      cursor: pointer;
      padding: 15px 20px; 
      height: 100%;
      background-color: #009688;
      i {
        color: #fff;
        font-size: 24px;
        transition: transform 0.2s ease-in-out;
        transform-origin: 50% 50%; 
      }
      &:hover {
        i {
          transform: rotate(90deg);
        }
      }
    }
  }
  .todo-list {
    list-style: none;
    overflow: hidden;
    li {
      padding: 20px;
      background-color: #607d8b;
      color: #fff;
      position: relative;
      &:not(:last-child) {
        margin-bottom: 10px;
      }
      .icons {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        .todo-remove {
          background-color: #f44336;
          padding: 20px 0;
          text-align: center;
          width: 60px;
          cursor: pointer;
          position: absolute;
          right: 0;
          i {
            transition: all 0.2s ease-in-out;
          }
          &:hover {
            i {
              animation-name: dance;
              animation-iteration-count: infinite;
              animation-duration: 0.5s;
            }
          }
        }
        .todo-done {
          background-color: #009688;
          padding: 20px 0;
          text-align: center;
          width: 60px;
          cursor: pointer;
          position: absolute;
          right: 60px;
          i {
            transition: transform 0.2s ease-in-out;
          }
          &:hover {
            i {
              transform: scale(1.3);
            }
          }
        }
      }
      &.no-tasks {
        background-color: #9e9e9e;
      }
      &.done {
        opacity: 0.7;
        text-decoration: line-through;
      }
    }
  }
}

@keyframes dance {
  0% {
    transform: rotate(20deg);
  }
  50% {
    transform: rotate(-20deg);
  }
  100% {
    transform: rotate(20deg);
  }
}
.overlay {
  background-color: #000000bf;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  transform-origin: right top;
  animation-name: rotationOn;
  animation-duration: 0.9s;
}
.alert {
  width: 800px;
  padding: 25px;
  border-radius: 0;
  color: #333;
  margin: 100px auto;
  font-size: 25px;
  font-family: sans-serif;
  background-color: #ffeb3b;
  position: absolute;
  z-index: 10;
  left: 0;
  right: 0;
  animation-name: fromUp;
  animation-duration: 1s;
  top: 100px;
  .close {
    position: absolute;
    top: 30px;
    right: 30px;
    cursor: pointer;
  }
  h3 {
    padding: 10px 0 20px;
    border-bottom: 1px solid #777;
  }
  p {
    padding: 25px 0 8px;
  }
}
.links {
  background-color: #333;
  padding: 8px 10px;
  position: fixed;
  left: 10px;
  bottom: 10px;
  color: #fff;
  text-decoration: none;
  font-family: sans-serif;
  font-size: 18px;
  transition: box-shadow 0.3s ease-in-out;
  &:hover {
    box-shadow: 0px 0px 10px 10px #00000024;
  }
}
.another {
  right: 10px;
  left: auto;
  background-color: #2196F3;
  &:hover {
    box-shadow: 0px 0px 10px 10px #2195f31c;
  }
}
@keyframes rotationOn {
  from {
    transform: rotate(90deg) scaleY(5);
  }
  to {
    transform: rotate(0) scaleY(5);
  }
}
@keyframes rotationOff {
  from {
    transform: rotate(0) scaleY(5);
  }
  to {
    transform: rotate(90deg) scaleY(5);
  }
}
@keyframes fromUp {
  from {
    top: -50%;
  }
  to {
    top: 100px;
  }
}
@keyframes toUp {
  from {
    top: 100px;
  }
  to {
    top: -50%;
  }
}
@keyframes afterLoad {
  from {
    margin-top: 300px;
    opacity: 0;
  }
  to {
    margin-top: 100px;
    opacity: 1;
  }
}
@media (max-width: 800px) {
  .todo-container {
    width: 98%;
    margin: 50px 1%;
  }
  .alert {
    width: 98%;
    margin: 1%;
  }
  .links, .another {
    display: block;
    position: static;
    margin: 20px 10px;
    padding: 20px;
  }
}